"use client";
import { useState } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Progress } from '@/components/ui/progress';

export default function SimulatorPage() {
  const { transactions } = useFinance();
  const [estimatedIncome, setEstimatedIncome] = useState<number>(0);

  // Calcular gastos fijos hist칩ricos promedio o del mes actual
  // Para este ejemplo, sumamos los marcados como 'isFixed' del mes actual
  const fixedExpenses = transactions
    .filter(t => t.type === 'expense' && t.isFixed)
    .reduce((acc, curr) => acc + curr.amount, 0);

  const remaining = estimatedIncome - fixedExpenses;
  const healthScore = estimatedIncome > 0 ? (remaining / estimatedIncome) * 100 : 0;

  return (
    <div className="container mx-auto p-4 max-w-2xl space-y-6">
      <h1 className="text-2xl font-bold">Simulador Mensual</h1>
      
      <Card className="bg-zinc-900 border-zinc-800">
        <CardHeader><CardTitle>Proyecci칩n</CardTitle></CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label>Ingreso Esperado este Mes</Label>
            <Input 
              type="number" 
              value={estimatedIncome} 
              onChange={(e) => setEstimatedIncome(Number(e.target.value))}
              className="bg-black border-zinc-700 text-xl"
            />
          </div>

          <div className="bg-zinc-950 p-4 rounded-lg border border-zinc-800 space-y-2">
            <div className="flex justify-between">
              <span className="text-zinc-400">Gastos Fijos Detectados</span>
              <span className="text-red-400 font-bold">- ${fixedExpenses.toLocaleString()}</span>
            </div>
            <div className="h-px bg-zinc-800 my-2" />
            <div className="flex justify-between text-lg">
              <span>Restante Proyectado</span>
              <span className={remaining >= 0 ? 'text-green-500 font-bold' : 'text-red-500 font-bold'}>
                ${remaining.toLocaleString()}
              </span>
            </div>
          </div>

          <div className="space-y-2">
            <Label>Salud Financiera (Liquidez)</Label>
            <Progress value={healthScore} className="h-2" />
            <p className="text-xs text-zinc-500 text-right">{healthScore.toFixed(0)}% libre</p>
          </div>
        </CardContent>
      </Card>

      <div className="text-sm text-zinc-500">
        * Los gastos fijos se calculan autom치ticamente bas치ndose en las transacciones que marcaste como "Gasto Fijo" en el historial.
      </div>
    </div>
  );
}